@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local("Roboto Regular"), local("Roboto-Regular"), url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}

@font-face {
  font-family: 'Roboto';
  font-style: italic;
  font-weight: 400;
  src: local("Roboto Italic"), local("Roboto-Italic"), url("../fonts/roboto-italic.woff2") format("woff2"), url("../fonts/roboto-italic.woff") format("woff");
}

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
}

@font-face {
  font-family: 'Roboto';
  font-style: italic;
  font-weight: 700;
  src: local("Roboto Bold Italic"), local("Roboto-BoldItalic"), url("../fonts/roboto-bolditalic.woff2") format("woff2"), url("../fonts/roboto-bolditalic.woff") format("woff");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

html,
body {
  height: 100%;
}

body {
  color: #333;
  background: #f0f0f0;
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
}

a {
  color: #2e99db;
}

a:hover {
  text-decoration: none;
}

.application {
  position: relative;
  margin: 0 auto;
  width: 584px;
  height: 100%;
}

.application__img {
  float: left;
  width: 33%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
}

.application__content {
  float: right;
  overflow: auto;
  padding: 20px;
  width: 67%;
  height: 100%;
}

.application__logos {
  position: relative;
  margin-bottom: 15px;
  font-size: 0;
}

.application__version {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 14px;
  white-space: nowrap;
}

.application__logo {
  display: inline-block;
  vertical-align: top;
}

.application__b2b {
  width: 70px;
}

.application__vendor {
  margin: 10px 0 0 20px;
  height: 50px;
}

.info {
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid rgba(51, 51, 51, 0.3);
}

.info__item {
  margin: 0 -7px;
  font-size: 0;
}

.info__label, .info__value {
  display: inline-block;
  vertical-align: top;
  padding: 4px 7px;
  font-size: 13px;
}

.info__label {
  width: 30%;
  text-align: right;
}

.info__value {
  width: 70%;
}

.warning {
  margin: 0 -7px 15px;
  font-size: 0;
}

.warning__label, .warning__value {
  display: inline-block;
  vertical-align: top;
  padding: 4px 7px;
  color: #c65047;
  font-size: 13px;
}

.warning__label {
  width: 30%;
  text-align: right;
}

.warning__value {
  width: 70%;
}

.client {
  padding: 15px;
  margin-bottom: 15px;
  border: 1px solid #fafafa;
  background: #e6e6e6;
}

.client__item {
  margin: 0 -7px;
  font-size: 0;
}

.client__label, .client__value {
  display: inline-block;
  vertical-align: top;
  padding: 2px 7px;
  width: 50%;
  font-size: 13px;
}

.client__label {
  text-align: right;
}

.footer {
  overflow: hidden;
  padding-top: 5px;
  margin: 0 -10px;
  font-size: 0;
}

.footer__copyright, .footer__button {
  display: inline-block;
  vertical-align: middle;
  padding: 0 10px;
}

.footer__copyright {
  padding-left: 110px;
  margin-left: -100px;
  width: 100%;
  font-size: 13px;
  text-align: right;
}

.footer__button {
  width: 100px;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 12px 20px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-size: 16px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s,background .175s,box-shadow .175s;
}

.btn_wide {
  width: 100%;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}
